import {
    ModuleWithProviders, NgModule,
    Optional, SkipSelf
} from '@angular/core';

import { CommonModule } from '@angular/common';

import {
    MatProgressSpinnerModule,
    MatDialogModule,
    MatButtonModule,
    MatIconModule,
    MatRadioModule
} from '@angular/material';

import {
    ReactiveFormsModule,
    FormsModule
} from '@angular/forms';

import { TagInputModule } from 'ngx-chips';

import { PolpMdIndicatorModal } from './indicator/modal.component';
import { PolpMdSpinnerServiceImpl } from './services/spinner-service.impl';

import { EmailFormComponent } from './email-form/email-form.component';

import { RadioGroupFieldControl } from './radio-group-field/radio-group-field.component';

@NgModule({
    declarations: [
        PolpMdIndicatorModal,
        EmailFormComponent,
        RadioGroupFieldControl
    ],
    imports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,

        MatProgressSpinnerModule,
        MatDialogModule,
        MatButtonModule,
        MatIconModule,
        MatRadioModule,

        TagInputModule
    ],
    exports: [
        PolpMdIndicatorModal,
        EmailFormComponent,
        RadioGroupFieldControl
    ],
    entryComponents: [
        PolpMdIndicatorModal,
        EmailFormComponent
    ],
    providers: [
        PolpMdSpinnerServiceImpl
    ]
})
export class PolpMdComponentsModule {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: PolpMdComponentsModule
        };
    }

    constructor(@Optional() @SkipSelf() parentModule: PolpMdComponentsModule) {
        if (parentModule) {
            throw new Error(
                'PolpMdComponentsModule is already loaded. Import it in the AppModule only');
        }
    }
}
